import { Component, h, Host, Prop } from '@stencil/core';
import { TP_CALENDAR_DAY } from './utils/calendar-day.constant';

@Component({
  tag: 'tp-calendar-day',
  styleUrl: 'calendar-day.scss',
  assetsDirs: ['assets'],
  shadow: true,
})
export class TpCalendarDay {
  /**
   * Define se o componente est치 no estado ativo.
   */
  @Prop({ reflect: true }) active = false;

  /**
   * Define se o componente est치 no estado concluido.
   */
  @Prop({ reflect: true }) checked = false;

  /**
   * Define se o componente est치 no estado desativado.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Define se o componente est치 no estado delineado.
   */
  @Prop({ reflect: true }) outlined = false;

  render() {
    const { active, checked, outlined, disabled } = this;

    return (
      <Host
        class={{
          'tp-calendar-day': true,
          'tp-calendar-day--active': active,
          'tp-calendar-day--checked': checked,
          'tp-calendar-day--disabled': disabled,
          'tp-calendar-day--outlined': outlined,
        }}
      >
        <div class="tp-calendar-day__date">
          <div class="tp-calendar-day__container">
            <ion-text class="tp-calendar-day__text">
              <slot></slot>
            </ion-text>

            <ion-icon
              class="tp-calendar-day__checked-icon"
              src={TP_CALENDAR_DAY.icons.check}
            ></ion-icon>
          </div>
        </div>
      </Host>
    );
  }
}
